<template>
  <div id = "out_container">
    <el-container>
      <el-header>
        <el-row>
          <el-col :span="2"><i class="el-icon-menu align-center" v-on:click="chooseSta()"></i>选择</el-col>
          <el-col :span="4" class="align-center">当前：{{this.fillFile}}</el-col>
          <el-col :span="12">
            <p style="font-size: 20px; padding: 0 20px;" class="align-center">填表</p>
          </el-col>
          <el-col :span="6">
            <i class="el-icon-check" style="float: right; padding: 20px;" v-on:click="formSubmit()"></i>
          </el-col>
        </el-row>
      </el-header>
      <el-main style="display: flex;">
        <el-form ref="form" label-width="80px" style="width: 500px; text-align: left;">
          <el-form-item v-for="item in items" :key="item.id" :label="item.itemName">
            <div v-if="item.type === 'text'">
              <el-input v-model="item.value" placeholder="请输入内容"></el-input>
            </div>
            <div v-if="item.type === 'num'">
              <el-input v-model="item.value" placeholder="请输入内容"></el-input>
            </div>
            <div v-if="item.type === 'date'">
              <el-date-picker
                v-model="item.value"
                type="date"
                placeholder="选择日期">
              </el-date-picker>
            </div>
            <div v-if="item.type === 'time'">
              <el-time-picker
                v-model="item.value"
                :picker-options="{
                  selectableRange: '00:00:00 - 23:59:59'
                }"
                placeholder="任意时间点">
              </el-time-picker>
            </div>
            <div v-if="item.type === 'longtext'">
              <el-input
                type="textarea"
                :autosize="{ minRows: 2, maxRows: 5}"
                placeholder="请输入内容"
                v-model="item.value">
              </el-input>
            </div>
            <div v-if="item.type === 'pic'">
              <div>
                <el-button size="small" type="primary" v-on:click="chooseFile(item)">选择</el-button>
                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                <img v-bind:src="item.value" style="height: 100px; wdith: 100px;"/>
              </div>
            </div>
            <div v-if="item.type === 'email'">
              <el-input v-model="item.value" placeholder="请输入内容"></el-input>
            </div>
            <div v-if="item.type === 'idcard'">
            <el-input
              v-on:change="checkInput(item)"
              v-model="item.value"
              placeholder="请输入内容"
              :clearable=true>
            </el-input>
            <div
              class="warning"
              v-for="warn in item.warn"
              :key="warn.id">
              {{warn}}
            </div>
          </div>
          <div v-if="item.type === 'zip'">
            <el-input
              v-on:change="checkInput(item)"
              v-model="item.value"
              placeholder="请输入内容"
              :clearable=true></el-input>
            <div
              class="warning"
              v-for="warn in item.warn"
              :key="warn.id">
              {{warn}}
            </div>
          </div>
            <div v-if="item.type === 'percent'">
            <el-input
              v-on:change="checkInput(item)"
              v-model="item.value"
              placeholder="请输入内容"
              :clearable=true>
              <template slot="append">%</template>
            </el-input>
            <div
              class="warning"
              v-for="warn in item.warn"
              :key="warn.id">
              {{warn}}
            </div>
          </div>
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>

    <el-dialog
        title="选择统计项目"
        :visible.sync="fileChooseDialog"
        :show-close=true
        :close-on-press-escape=false
        width="500px">
        <span>
          <div style="display: flex; justify-content: center;">
            <table style="width: 100%; padding-left: 15px; padding-right: 15px;">
              <tr v-for="item in staItems" :key="item.id" style="width: 100%">
                <td>{{item}}</td>
                <td>
                  <div v-on:click="choose(item)">
                    <span>选择</span>
                  </div>
                </td>
              </tr>
            </table>
          </div>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      items: [],
      fillFile: '',
      staItems: [],
      fileChooseDialog: false
    }
  },
  created: function () {
    Axios.get('http://127.0.0.1:5050/api/stadata').then(response => {
      this.staItems = response.data
      console.log(response.data)
      this.fileChooseDialog = true
    })
  },
  methods: {
    formSubmit () {
      Axios.post('http://127.0.0.1:5050/api/submit', this.items)
    },
    choose (item) {
      this.fillFile = item
      Axios.get('http://127.0.0.1:5050/api/findsta/' + item).then(response => {
        console.log(response.data)
        this.items = response.data[0].staContent
        this.fileChooseDialog = false
      })
    },
    chooseSta () {
      this.fileChooseDialog = true
    }
  }
}
</script>

<style>
  .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    float: left;
    height: auto;
    /* line-height: 60px; */
  }
  .header i {
    padding: 20px;
  }
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    /* line-height: 160px; */
  }
  .mainContent {
    /* margin: auto; */
    text-align: center;
    background-color: #99CCFF;
    padding: 0;
    display: block;
  }
  .el-icon-circle-plus-outline{
    text-align: center;
    margin: 12px;
  }
  .newitem{
    background-color: #B3C0D1;
    padding: 10px 0;
  }
  .mainContainer {
    max-height: 100vh;
  }
  .item{
    margin-bottom: 7px;
  }
  .itemName{
    width: 150px;
  }
  .item p{
    width: 150px;
    margin: 0;
    padding: 10px 5px 10px 5px;
  }
  .item i{
  margin: 10px;
  }
  body{
    margin: 0;
    background-color: beige;
  }
  #out_container {
    width: 70%;
    height: 100%;
  }
  html, body{
    height: 100%;
    width: 100%;
    max-width: 1024px;
    min-width: 800px;
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
  .el-container{
    height: 100%;
  }
  .align-center {
    -webkit-margin-before: 0 !important;
    -webkit-margin-after: 0 !important;
    margin-top: 0;
    margin-bottom: 0;
    line-height: 60px;
  }
</style>



